@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@500&family=Playfair+Display:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Playfair+Display:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Work+Sans:wght@500&display=swap);
* {
  margin: 0;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  overflow-x: hidden;
  background: url(/github.io/resume-reactjs/static/media/bg.54815803.png)
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
React.StrictMode {
  background-color: #777;
}
/* Fonts */
    .playfair {
        font-family: 'Playfair Display', serif !important;
    }
    .montserrat {
        font-family: "Montserrat", sans-serif !important;
    }
    .work_sans {
        font-family: 'Work Sans', sans-serif;
    }
/* Fonts ends */
:root {
    --main-color: #ffc500;
    --shadow:  0px 2px 92px 0px rgba(0,0,0, 0.13);
}

/* Margins and Padding */
.top_60 {
    margin-top: 60px !important;
}
.top_30 {
    margin-top: 30px !important;
}
.mb_30 {
    margin-bottom: 30px !important;
}
.p-30 {
    padding: 30px !important;
}
.pb-45 {
    padding-bottom: 45px !important;
}
/* Margins and Padding ends */

@media (max-width: 991px) {
    .top_60 {
        margin-top: 30px;
    }
}
.main_content {
    background: #fff;
    box-shadow: 0px 2px 92px 0px rgba(0,0,0, 0.13);
    box-shadow: var(--shadow);
    border-radius: 6px;
    padding: 30px;
}
.container_shadow {
    box-shadow: 0px 2px 92px 0px rgba(0,0,0, 0.13);
    -webkit-box-shadow: 0px 2px 92px 0px rgba(0,0,0, 0.13);
    -moz-box-shadow: 0px 2px 92px 0px rgba(0,0,0, 0.13);
}
.section_title {
    position: relative;
    display: inline-block;
    padding: 0 3px;
}
.section_title  .section_title_text {
    font-size: 22px;
    font-weight: 600;
    position: relative;
    margin: 0;
}
.section_title span {
    position: absolute;
    left: 0;
    height: 7px;
    bottom: 0;
    width: 100%;
    opacity: .4;
    background: #ffc500;
    background: var(--main-color);
}
 .footer {
     background: #1e1e1e;
     color: #fff;
     display: flex;
     flex-wrap: wrap;
     align-items: center;
     justify-content: space-between;
     min-height: 70px;
     border-radius: 8px;
     padding: 20px 25px;
     margin-top: 30px;
 }
 .footer .footer_copyright {
     font-size: small;
     text-align: end;
     color: #9c9c9c;
 }
 .footer .footer_copyright > a {
     text-decoration: none;
     color: #fff;
 }
.custom_btn {
    background: var(--main-color) !important;
    font-size: 13px !important;
    font-weight: 600 !important;
    border-radius: 50px !important;
    padding: 1px 12px !important;
    line-height: 35px !important;
    min-width: 100px !important;
    outline: none !important;
    border: 0 !important;
}
.custom_btn .btn_icon_container {
    color: #000 !important;
    background: #fff !important;
    border-radius: 50px !important;
    height: 27px !important;
    width: 27px !important;
    line-height: 29px !important;
    text-align: center !important;
    display: flex !important;
    justify-content: center !important;
}
.custom_btn .btn_icon_container > .MuiSvgIcon {
    font-size: 13px !important;
}
.custom_btn .btn_text {
    font-size: 16px !important;
    text-transform: none;
    text-align: center !important;
    margin: 0 5px 0 5px !important;
    width: 100% !important;
}

.header {
    margin-bottom: 30px !important;
    background: #fff !important;
    padding: 0 22px 0 0 !important;
    border-radius: 6px !important;
    box-shadow: 0px 2px 92px 0px rgba(0,0,0, 0.7);
}
.header .header_navLink {
    padding: 0 !important;
}
.header .header_link {
    font-weight: 600 !important;
    color: darkslategray !important;
}
.navbar-nav .nav-link {
    text-transform: uppercase !important;
    padding-left: 10px;
}
.header .header_link:hover {
    font-weight: 500;
    color: var(--main-color) !important;
}
.header .header_active_link {
    font-weight: 500;
    color: var(--main-color) !important;
}
.header .header_home {
    background: var(--main-color);
    padding: 22px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-top-left-radius: 6px;
    border-bottom-left-radius: 6px;
}
.header_left {
    margin-right: auto;
}
.header .header_right {
    display: flex !important;
    justify-content: center !important;
}
.header .header_right > a > .MuiSvgIcon-root {
    font-size: 16px !important;
    margin-right: 12px;
    color: black;
}
@media (max-width: 576px) {
    .header .header_right {
        margin-bottom: 10px;
    }
}
.timeline {
    padding: 0 !important;
}

/* header */

/* header title */

.timeline .timeline_header {
    margin-top: 7px;
}
.timeline .timeline_firstItem {
    min-height: 80px !important;
}
.timeline .MuiTimelineItem-root {
    min-height: 0px;
}
.timeline .MuiTimelineItem-missingOppositeContent:before {
    display: none;
}
.timeline .timeline_dot_header {
    color: black;
    background: var(--main-color);
    font-size: small;
    padding: 10px;
}

/* remaining timeline */
.timeline .timeline_dot {
    color: black;
    border-color: var(--main-color);
    padding: 2px;
    background-color: white;
}
.timeline .MuiTimelineConnector-root {
    background-color: #eee;
}
.timeline .timeline_dot_header > .MuiSvgIcon-root{
    font-size: 20px;
    color: darkslategray;
}
.separator_padding {
    padding-left: 17px;
}
.timeline .timeline_content{
    padding-top: 0;
    padding-bottom: 20px;
}
.profile {
    background: #fff;
    border-radius: 6px;
    width: 100%;
    display: inline-block;
}
.profile_name {
    line-height: 18px;
    padding: 20px;
}
.profile_name .name {
    text-transform: uppercase;
    font-size: 17px;
    font-weight: bold;
}
.profile_name .title {
    font-size: 13px;
    color: #777;
}
.profile_image {
    margin-top: -18px;
    -webkit-clip-path: polygon(0 9%, 100% 0, 100% 94%, 0% 100%);
    clip-path: polygon(0 9%, 100% 0, 100% 94%, 0% 100%); 
}
.profile_image img {
    width: 100%;
}

.profile_information {
    top: 0;
    left: 0;
    margin: -44px 10px 30px;
}
.profile_information .timeline .timeline_content {
    margin-top: 2px;
    margin-bottom: 2px;
}
.timelineItem_text {
    font-size: 13.5px !important;
    color: #787878 ;
    font-weight: 400;
}
.timelineItem_text > a {
    text-decoration: none;
    color: #787878;
}
.timelineItem_text > span {
    color: black;
}.profile .button_container {
    width: 100%;
    display: flex;
    justify-content: center;
    margin-top: 20px;
}
.section_title span {
    width: 100px;
}
/* Tabs */
.customTabs .active {
    color: var(--main-color);
}
.customTabs .customTabs_item{
    padding: 0px !important;
    min-width: 0px !important;
    margin-right: 20px !important;
    text-transform: inherit !important;
    font-size: 14px !important;
    font-weight: 400 !important;
    outline-width: 0px !important;
}

/* Cards */
.customCard {
    height: 100%;
    max-width: 400px;
    width: 100%;
    box-shadow: 0 0 48px 0 rgba(100, 100, 100, 0.26) !important;
    border-radius: 5px;
    overflow: hidden;
    outline-width: 0px !important;
    margin: 10px;
}
.customCard .customCard_image {
    height: 140px;
}
.customCard .customCard_title {
      font-weight: 500 !important;
}
.customCard .customCard_caption {
    color: gray;
}

/* Project Dialog */

.projectDialog_image {
    max-height: 300px;
    width: 100%;
    object-fit: cover;
    object-position: center center;
}
.projectDialog_description {
    margin-top: 10px !important;
}
.projectDialog_actions {
    margin-bottom: 10px !important;
}
.projectDialog_icon {
    color: darkslategray;
    margin: 0px 12px;
    cursor: pointer;
}
.projectDialog_icon:hover {
    color: var(--main-color);
}

@media (max-width: 576px) {
    .customTabs .customTabs_item {
        font-size: 10px !important;
    }
    .customCard .customCard_title {
        font-size: 12px !important;
    }
}

/* Objective */
.about_text {
    color: darkslategray;
    line-height: 22px !important;
}
/* Objective Ends */

/* Timeline */
    .resume_timeline .timeline_company {
        font-size: 20px !important;
        font-weight: 600 !important;
        font-family: 'Playfair Dispay', serif !important;
    }
    .resume_timeline .timeline_title {
        font-weight: 500 !important;
        font-family: 'Montserrat', sans-serif;
    }
    .resume_timeline .timeline_date {
        color: darkslategray !important;
        margin: 0 !important;
        font-family: 'Playfair Dispay', serif !important;
    }
    .resume_timeline .timeline_description {
        color: gray;
        margin-top: 10px !important;
        font-size: small;
    }

/* Timeline Ends */
.msg {
    margin-top: 20px !important;
}
.contactInfo_item {
    margin-bottom: 15px !important;
    color: #989898;
    font-size: 14px !important;
}
.white {
    color: #fff;
}
.contactInfo_s_icons {
    color: #989898;
    margin: 7px !important;
}
.contactInfo_s_icons:hover {
    color: var(--main-color);
    margin: 7px !important;
}
.contactInfo_title {
    color: black;
    font-family: 'Playfair Display', serif !important;
}
.about_me_text {
    color: darkslategray;
    font-size: smaller;
    line-height: 22px !important;
    white-space: pre-line;
    font-family: 'Work Sans' sans-serif;
}
/* Services section */
.service {
    height: 100%;
    text-align: center;
}
.service .service_title {
    font-size: 16px;
    font-weight: 500;
    line-height: 35px;
    text-align: center;
    margin-left: 9px !important;
}
.service .service_description {
    color: #989898;
    border-color: var(--main-color) !important;
    margin-right: 5px;
    text-align: center;
}
.service_icon > .MuiSvgIcon-root {
    font-size: 46px !important;
    color: darkslategray;
    font-weight: 300;

}

/* Skills Section */
.skill {
    border-radius: 8px;
    padding: 20px;
    box-shadow: 0 0 48px 0 rgba(100, 100, 100, 0.26) !important;
    min-height: 150px;
    height: 100%;
}
.skill .skill_title {
    font-size: 14px !important;
    font-weight: 500;
    line-height: 35px;
}
.skill .skill_description {
    display: flex;
    align-items: center;
    color: #989898;
    line-height: 18px;
    font-weight: 400;
}
.skill .timeline_dot {
    border-color: var(--main-color);
    padding: 2px !important;
    margin-right: 5px;
}
.graybg {
    background: #fafafa;
}
