 .footer {
     background: #1e1e1e;
     color: #fff;
     display: flex;
     flex-wrap: wrap;
     align-items: center;
     justify-content: space-between;
     min-height: 70px;
     border-radius: 8px;
     padding: 20px 25px;
     margin-top: 30px;
 }
 .footer .footer_copyright {
     font-size: small;
     text-align: end;
     color: #9c9c9c;
 }
 .footer .footer_copyright > a {
     text-decoration: none;
     color: #fff;
 }